@charset "UTF-8";
/* Base */
/* -------------------------------------------------------- */
body {
  font-family: sans-serif;
  line-height: 1.5;
  color: #222222;
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}

/* スクリーンリーダー用テキスト */
/* --------------------------------------------------------- */
.sr-only {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
}

/* Parts Grid */
/* -------------------------------------------------------- */
.parts-grid {
  display: grid;
  grid-template-columns: minmax(8vw, 1fr) minmax(auto, 1088px) minmax(8vw, 1fr);
}
.parts-grid > * {
  grid-column: 2;
}

/* List */
/* -------------------------------------------------------- */
.list > .grid12 {
  row-gap: 32px;
}
.list > .grid12 h2 + p {
  margin-bottom: 8px;
}

/* List Inner Items */
@media (min-width: 768px) {
  .list h2,
.list h2 + p {
    grid-column: span 8;
  }
}
.list h2 {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 26px;
  font-weight: 400;
  color: #444444;
}
@media (min-width: 768px) {
  .list h2 {
    font-size: 32px;
  }
}
.list h2 + p {
  font-size: 14px;
  line-height: 1.8;
}
@media (min-width: 768px) {
  .list h2 + p {
    font-size: 16px;
  }
}
.list ul.grid12 {
  row-gap: 20px;
}
@media (min-width: 768px) {
  .list ul.grid12 {
    row-gap: 28px;
  }
}
.list ul img {
  width: 100%;
  height: 155px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .list ul img {
    height: 265px;
  }
}
@media (min-width: 768px) {
  .list ul.pc-col-3 img {
    height: 353px;
  }
}
.list ul h3 {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 16px;
  font-weight: 500;
}
@media (min-width: 768px) {
  .list ul h3 {
    font-size: 18px;
  }
}

/* 12分割グリッド Type:Normal */
/* Mobile-Grids: 4, PC-Grids: 12 */
/* -------------------------------------------------------- */
.grid12 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 10px;
}
@media (min-width: 768px) {
  .grid12 {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 16px;
  }
}
.grid12 > * {
  grid-column: 1/-1;
}

/* 12分割グリッド：カラム数 */
.mobile-col-2 > * {
  grid-column: span 2;
}
.mobile-col-2.pc-col-3 > :first-child {
  grid-column: span 4;
}

@media (min-width: 768px) {
  .pc-col-2 > * {
    grid-column: span 6;
  }
}

@media (min-width: 768px) {
  .pc-col-3 > * {
    grid-column: span 4;
  }
}

@media (min-width: 768px) {
  .pc-col-4 > * {
    grid-column: span 3;
  }
}

/* 12分割グリッド Type:Frame */
/* Mobile-Grids: 4, PC-Grids: 12 */
/* -------------------------------------------------------- */
.frame .grid12 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 10px;
}
@media (min-width: 768px) {
  .frame .grid12 {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 16px;
  }
}
.frame .grid12 > * {
  grid-column: 1/-1;
}

/* 12分割グリッド：カラム数 */
.frame .mobile-col-2 > * {
  grid-column: span 2;
}
.frame .mobile-col-2.pc-col-3 > :first-child {
  grid-column: span 4;
}

@media (min-width: 768px) {
  .frame .pc-col-2 > * {
    grid-column: span 6;
  }
}

@media (min-width: 768px) {
  .frame .pc-col-3 > * {
    grid-column: span 4;
  }
}

@media (min-width: 768px) {
  .frame .pc-col-4 > * {
    grid-column: span 3;
  }
}

/* Border */
.frame {
  border: 1px solid #16a6cb;
  padding: 40px 15px;
  margin: 0 -15px;
}
@media (min-width: 768px) {
  .frame {
    padding: 60px 0;
    margin: 0;
  }
}
@media (min-width: 768px) {
  .frame h2,
.frame h2 + p {
    grid-column: 2/span 7;
  }
}
@media (min-width: 768px) {
  .frame ul {
    grid-column: 2/-2;
  }
}

/* Index page */
/* -------------------------------------------------------- */
/* List */
/* -------------------------------------------------------- */
/* Category */
.list-category {
  padding-top: 80px;
  padding-bottom: 80px;
  overflow: hidden;
}
@media (min-width: 768px) {
  .list-category {
    padding-bottom: 86px;
  }
}
.list-category > .grid12 {
  position: relative;
}
.list-category > .grid12::before {
  position: absolute;
  content: url(~@img/sun.svg);
  top: -80px;
  right: -120px;
  z-index: -1;
}
@media (min-width: 768px) {
  .list-category > .grid12::before {
    right: 17px;
  }
}

/* Experience */
.list-experience {
  padding-top: 80px;
  padding-bottom: 80px;
  background-color: #eefbe3;
}

/* Popular */
.list-popular {
  padding-top: 40px;
  padding-bottom: 40px;
}
@media (min-width: 768px) {
  .list-popular {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
.list-popular ul p {
  font-size: 16px;
}
@media (min-width: 768px) {
  .list-popular ul p {
    font-size: 18px;
  }
}

/* Content-page */
/* -------------------------------------------------------- */
/* Product Information */
.item.parts-grid {
  row-gap: 44px;
  padding-top: 20px;
  padding-bottom: 64px;
}
@media (min-width: 768px) {
  .item.parts-grid {
    row-gap: 80px;
    padding-top: 30px;
    padding-bottom: 80px;
  }
}
.item.parts-grid .item-photo {
  grid-column: 1/-1;
}
@media (min-width: 768px) {
  .item.parts-grid .item-photo {
    grid-column: 2;
  }
}
.item.parts-grid + .list-popular {
  padding-top: 0;
}

/* Grid-12 */
.item .grid12 {
  row-gap: 44px;
}
@media (min-width: 768px) {
  .item .item-body {
    grid-column: span 6;
  }
}
@media (min-width: 768px) {
  .item .item-order {
    grid-column: span 5/-1;
  }
}

/* Text */
.item-body h1 {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 26px;
  font-weight: 400;
  margin-bottom: 32px;
}
@media (min-width: 768px) {
  .item-body h1 {
    font-size: 44px;
    margin-bottom: 38px;
  }
}
.item-body h1 + p {
  font-size: 14px;
  line-height: 1.8;
  margin-bottom: 44px;
}
@media (min-width: 768px) {
  .item-body h1 + p {
    font-size: 16px;
  }
}
.item-body ul {
  display: grid;
  row-gap: 23px;
}
.item-body ul h3 {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 7px;
}
@media (min-width: 768px) {
  .item-body ul h3 {
    font-size: 18px;
  }
}
.item-body ul p {
  font-size: 14px;
}
@media (min-width: 768px) {
  .item-body ul p {
    font-size: 16px;
  }
}

/* Decoration */
.item {
  overflow: hidden;
}

.item-body {
  position: relative;
}
.item-body::before {
  position: absolute;
  content: url(~@img/sun.svg);
  top: -85px;
  right: -133px;
  z-index: -1;
}
@media (min-width: 768px) {
  .item-body::before {
    top: -209px;
    left: -193px;
    right: auto;
  }
}

/* list Icon */
.item-body li {
  display: grid;
  grid-template-columns: 54px 1fr;
}
.item-body li .fa-fw {
  grid-row: span 2;
  font-size: 30px;
  color: #6abeeb;
}

/* Order Form */
.item-order {
  padding: 44px 23px;
  border-radius: 10px;
  background-color: #eeeeee;
}
@media (min-width: 768px) {
  .item-order {
    align-self: start;
    padding: 54px 8%;
  }
}
.item-order .price {
  font-size: 24px;
  font-weight: bold;
  color: #c50a0a;
  padding-bottom: 22px;
  border-bottom: 1px solid #cccccc;
}
.item-order .price span {
  font-size: 10px;
  font-weight: normal;
  color: #222222;
  margin-left: 5px;
}
.item-order .quantity {
  display: block;
  font-size: 14px;
  font-weight: bold;
  padding: 44px 0;
}
.item-order .quantity select {
  width: 154px;
  padding: 14px 20px 13px;
  margin-left: 31px;
  border-radius: 10px;
  border: 1px solid #cccccc;
  background-color: #ffffff;
  appearance: none;
  background-image: url(~@img/select.svg);
  background-repeat: no-repeat;
  background-position: right 15px center;
  font-family: inherit;
}
.item-order .btn {
  width: 100%;
  height: 75px;
  background-color: #c50a0a;
  border-radius: 4px;
  border: none;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  font-size: 16px;
  font-weight: bold;
  color: #ffffff;
  text-align: center;
  cursor: pointer;
  appearance: none;
}
@media (min-width: 768px) {
  .item-order .btn {
    font-size: 18px;
  }
}
.item-order .btn .icon-cart {
  font-size: 21px;
  vertical-align: middle;
  margin-right: 14px;
}
@media (min-width: 768px) {
  .item-order .btn .icon-cart {
    font-size: 30px;
  }
}

/* Footer */
/* -------------------------------------------------------- */
.footer.parts-grid {
  background-color: #f0f9fe;
  border-top: 1px solid #6abeeb;
  padding: 80px 0;
  row-gap: 74px;
}
@media (min-width: 768px) {
  .footer.parts-grid {
    row-gap: 99px;
  }
}
.footer.parts-grid .footer-nav.grid12 {
  row-gap: 40px;
}
.footer.parts-grid .footer-nav.grid12 h4 {
  color: #888888;
  margin-bottom: 20px;
}
.footer.parts-grid .footer-nav.grid12 ul {
  display: grid;
  row-gap: 7px;
}
.footer.parts-grid .footer-site.grid12 {
  row-gap: 36px;
}
.footer.parts-grid .footer-site.grid12 .profile p {
  font-size: 12px;
  margin-top: 15px;
}
@media (min-width: 768px) {
  .footer.parts-grid .footer-site.grid12 .copyright {
    justify-self: end;
    align-self: end;
    text-align: right;
  }
}
.footer.parts-grid .footer-site.grid12 .copyright ul {
  display: grid;
  grid-auto-flow: column;
  justify-content: start;
  column-gap: 18px;
  font-size: 12px;
}
@media (min-width: 768px) {
  .footer.parts-grid .footer-site.grid12 .copyright ul {
    column-gap: 28px;
  }
}
.footer.parts-grid .footer-site.grid12 .copyright p {
  font-size: 12px;
  margin-top: 16px;
}

/* Header */
/* -------------------------------------------------------- */
.header.parts-grid {
  padding-top: 26px;
}
@media (min-width: 768px) {
  .header.parts-grid {
    padding-top: 39px;
  }
}
.header.parts-grid .header-nav.grid12 {
  row-gap: 27px;
  align-items: center;
}
@media (min-width: 768px) {
  .header.parts-grid .header-nav.grid12 img {
    grid-column: span 2;
  }
}
.header.parts-grid .header-nav.grid12 ul {
  grid-column: span 3;
  display: grid;
  grid-auto-flow: column;
  justify-content: start;
  column-gap: 20px;
  font-size: 14px;
  color: #127791;
}
@media (min-width: 768px) {
  .header.parts-grid .header-nav.grid12 ul {
    grid-column: span 9;
    justify-self: end;
    gap: 40px;
    font-size: 16px;
  }
}
.header.parts-grid .header-nav.grid12 .cart {
  grid-column: span 1;
  justify-self: end;
  font-size: 23px;
  color: #127791;
}
@media (min-width: 768px) {
  .header.parts-grid .header-nav.grid12 .cart {
    font-size: 30px;
  }
}

/* Top-page Header */
/* -------------------------------------------------------- */
.top .header.parts-grid {
  background-image: url(~@img/illustration.jpg);
  background-size: cover;
  background-position: center bottom;
}
.top .header.parts-grid .catch.grid12 {
  padding-top: 195px;
  padding-bottom: 226px;
  row-gap: 32px;
}
.top .header.parts-grid .catch.grid12 h1 {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 32px;
  font-weight: 400;
}
@media (min-width: 768px) {
  .top .header.parts-grid .catch.grid12 h1 {
    grid-column: span 6;
    min-width: 8em;
    font-size: 54px;
  }
}
.top .header.parts-grid .catch.grid12 a {
  width: 260px;
  padding: 13px 0 17px;
  border-radius: 4px;
  border: 2px solid #ffffff;
  background-color: #16a6cb;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  font-family: "Noto Sans JP", sans-serif;
  font-size: 18px;
  font-weight: 500;
  color: #ffffff;
  text-align: center;
}

/* Divider */
.header {
  position: relative;
}

.custom-shape-divider-bottom-1621262427 {
  grid-column: 1/-1;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
}
.custom-shape-divider-bottom-1621262427 svg {
  position: relative;
  display: block;
  width: calc(134% + 1.3px);
  height: 129px;
}
.custom-shape-divider-bottom-1621262427 .shape-fill {
  fill: #FFFFFF;
}

/* Content-page Header */
/* -------------------------------------------------------- */
.content .header.parts-grid .header-nav ul {
  color: #555555;
}
.content .header.parts-grid .header-nav .cart {
  color: #555555;
}